<template>
    <div class="container">
        <div class="main__pics" v-if="pics">
            <div class="main__pics-item" v-for="pic in pics" :key="pic.id">
                <p>ID: {{ pic.id }}</p>
                <p class="text">Title: {{ pic.title }}</p>
                <img class="pics" :src="pic.url" alt="">
            </div>
        </div>
        <div v-else class="Loading">
            Идет загрузка...
        </div>
    </div>
</template>

<script>
    export default {
       
        created() {
            this.getPics()
        },
        computed: {
            pics() {
            return this.$store.state.pics
            }
        },
        methods:{
            getPics() {
            this.$store.dispatch('getPics')
            }
        }
    }   
</script>
