import { createRouter, createWebHistory } from 'vue-router'


import Pictures from '@/pages/Pictures.vue'
import Home from '@/pages/Home.vue'


export const routers = createRouter({
    history: createWebHistory(),
    routes: [
        { path: '/', name: 'Pics', component: Pictures },
        { path: '/home', name: 'HomePage', component: Home },
        { path: '/pictures', name: 'Pictures', component: () => import('@/pages/Pictures.vue')},
        { path: '/users', name: 'Users', component: () => import('@/pages/Users.vue')},
        { path: '/todos', name: 'Todos', component: () => import('@/pages/Todos.vue')},
        { path: '/posts', name: 'Posts', component: () => import('@/pages/Posts.vue')},
        { path: '/about/:id', name: 'UsersItem', component: () => import('@/pages/UsersItem.vue')},
    ]
})